<template>
	<view class="box">
		<view class="info" v-if="num&&num!='null'">
			<view>{{company}}</view>
			<view @tap="copy(num)">{{num}} <text class="iconfont iconfuzhi-"></text></view>
		</view>
		<view class="logistics">
			<uni-steps :options="list" active-color="#FF9E97" direction="column" />
		</view>
	</view>
</template>

<script>
	import uniSteps from './components/uni-steps.vue'
	let com = ""; //快递公司编号 小写
	let phone = ""; //手机号
	export default {
		data() {
			return {
				num: "",//快递号
				list: [],
				company:""
			}
		},
		components: {
			uniSteps
		},
		onLoad(option) {
			uni.showLoading({
				title:"加载中"
			})
			setTimeout(function(){
				uni.hideLoading()
			},500)
			com=option.com.toLowerCase()
			this.num=option.num
			phone=option.phone
			this.loadLogistics()
		},
		methods: {
			loadLogistics() {
				this.$api.get('/express/synQueryData', {
					params: {
						com: com,
						num: this.num,
						phone: phone
					}
				}).then(res => {
					if (res.code == 200) {
						let data=res.data;
						this.company=data.company+'快递'
						if(data.jsonObject&&data.jsonObject.data.length>0){
							this.list = data.jsonObject.data
						}else {
							this.list = [{
								context: "暂无物流信息",
								ftime: "",
								time: ""
							}]
						}
					} else {
						this.list = [{
							context: "暂无物流信息",
							ftime: "",
							time: ""
						}]
					}
				})
			},
			copy(data){
				// #ifndef H5
				uni.setClipboardData({
				    data: data,
				    success: function () {
						// app跟小程序会自动弹出提示
				    }
				});
				// #endif
			}
		}
	}
</script>

<style lang="scss">
	.box {
		width: 100%;
		height: auto;

		.info {
			display: flex;
			margin: 30rpx 30rpx;
			padding: 10rpx 30rpx;
			border-radius: 20rpx;
			background: white;
			font-size: 26rpx;
			display: flex;
			justify-content: space-between;
			.iconfont {
				margin-left: 20rpx;
				font-size: 24rpx;
				color: #666;
			}
			
		}

		.logistics {
			background: white;
			margin: 30rpx 30rpx;
			border-radius: 20rpx;
		}
	}
</style>
